.slide-next.enter {
  opacity: 0;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}

.slide-next.enter-active {
  opacity: 1;
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
  transition: all 500ms linear;
}

.slide-next.exit {
  opacity: 1;
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
}

.slide-next.exit-active {
  opacity: 0;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  transition: all 500ms linear;
}

.slide-pre.enter {
  opacity: 0;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}

.slide-pre.enter-active {
  opacity: 1;
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
  transition: all 500ms linear;
}

.slide-pre.exit {
  opacity: 1;
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
}

.slide-pre.exit-active {
  opacity: 0;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  transition: all 500ms linear;
}

.nav.enter {
  opacity: 0;
}

.nav.enter-active {
  opacity: 1;
  transition: all 500ms linear;
}

.nav.exit {
  opacity: 1;
}

.nav.exit-active {
  opacity: 0;
  transition: all 500ms linear;
}

